html {
  font-size: 18px;
  margin:0;
}
html,body {padding:0; margin:0;}
body {
  font-size: 1rem;
  font-family: $font-regular, Arial, Helvetica, sans-serif;
  font-weight: 300;
  line-height: 1.5;
  color: $black;

  padding-top:150px;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 1rem;
  display: block;
  max-width: calc(100% - 2rem);
}
hr {
  border:none;
  display: block;
  margin-top:4rem;
}
.footer{
  background-color:$grey;
  width:100%;
  padding:2rem 0;
  .container{
    display:flex;
    justify-content: space-between;
    flex-wrap:wrap;
  }
  &__info{
    font-size:0.6rem;
    padding:0.25rem 0;
  }
}

img {
  max-width: 100%;
}
@media screen and (min-width: 1024px) {
  .container {
    max-width: calc(100% - 6rem);
  }
}

@media screen and (min-width: 1200px) {
  html {
    font-size: 20px;
  }
  .container {
    max-width: calc(100% - 24rem);
  }
  body{
    padding-top:140px;
  }
}

