
.tile {
    display: grid;
    grid-template-columns: 1fr;
    column-gap: 2rem;
    row-gap: 2rem;
    grid-template-areas: 'tile-image' 'tile-content';
    .tile__image {
      grid-area: tile-image;
      overflow: hidden;
      box-shadow: 1px 1px 16px 1px rgba(0, 0, 0, 0.2);
      height:fit-content;
      img {
        object-fit: cover;
      }
    }
    .tile__content {

      grid-area: tile-content;
      place-self: center;
      p{
        margin-top:0;
      }
    }
    &--text{
      grid-template-columns: 1fr;
      .tile__title{
        grid-area: tile-image;
        background-color:$primary;
        padding:2rem;
        color:#fff;
        height:fit-content;
        box-shadow: 1px 1px 16px 1px rgba(0, 0, 0, 0.2);
      }
    }
  }

  @media screen and (min-width: 1024px) {
    .tile {
      grid-template-columns: 1fr 1fr;
      grid-template-areas: 'tile-image tile-content';
  
      &--text {
        grid-template-columns: 2fr 3fr;
      }

    &--reverse{

      grid-template-areas: 'tile-content tile-image';
      }
    }
  }