$font-path: "../fonts/" !default;
@font-face {
  font-family: "OpenSans";
  src: url($font-path + "OpenSans-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Poppins-Bold";
  src: url($font-path + "Poppins-SemiBold.woff2") format("woff2");
}
@font-face {
  font-family: "Poppins-Thin";
  src: url($font-path + "Poppins-Thin.woff2") format("woff2");
}
$font-regular: "OpenSans";
$heading: "Poppins-Bold";
$heading-thin: "Poppins-Thin";


h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $heading;
}
h1 {
  font-size: 2.5rem;
  margin: 1rem 0;
}
h2 {
  font-size: 2rem;
  margin: 1rem 0;
}
h3 {
  font-size: 2rem;
  margin: .75rem 0;
}
h4 {
  font-size: 1.5rem;
  margin: .5rem 0;
}
h5 {
  font-size: 1.25rem;
  margin: .5rem 0;
}
h6 {
  font-size: 1.15rem;
  margin: .5rem 0;
}
.txt-thin{
    font-family:$heading-thin;
}
