.header {
    z-index:9999999999;
  width: 100%;
  background-color: #fff;
  box-shadow: 1px 1px 16px 1px rgba(0, 0, 0, 0.2);
  position:fixed;
  top:0;
  display:flex;
  .container {
    padding: 1rem 0;
    display: flex;
    flex-direction: row;
  }
  &__title {
    display: flex;
    flex-direction: row;
    align-items: center;
    img {
      padding-right: 1rem;
    }
    a {
      display: flex;
      flex-direction: row;
      align-items: center;
    }
  }

  a,
  a h4 {
    text-decoration: none;
    color: $black;
  }
}
.main-nav {
  order: 1;
  display: flex;
  justify-content: flex-end;

  &__overlay {
    &.is-open{
    }
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    transition:opacity ease-in 400ms;
    opacity: 1;

    &.is-closed {
        opacity: 0;
        visibility:hidden
      }
  }
  &__trigger {
    display: flex;
    align-items: center;
    cursor: pointer;
    font-family: $heading;
    font-size: 0.8rem;
    text-transform: uppercase;
    color: $primary;
    opacity: 0.75;
    transition: all ease-in 400ms;
    &:hover {
      opacity: 1;
      transform: scale(1.1);
    }
  }
  &__bar {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0 10px;

    .dot {
      width: 24px;
      height: 4px;
      border-radius: 4px;
      background-color: $primary;
      display: block;
      margin: 2px 0;
    }
  }
  &__list {
    background-color: #fff;
    border-radius: 10px;
    list-style-type: none;
    margin: auto;
    min-width: 280px;
    display: flex;
    place-self: center;
    flex-direction: column;
    align-items: center;
    padding-left: 0;
    padding: 1rem;

    li {
      width: 100%;

      a {
        display: block;
        text-align: center;
        text-decoration: none;
        color: #333;
        text-transform: uppercase;
        font-family: $heading;
        font-size: 0.8rem;
        padding: 1rem;
        border-bottom: 1px solid $grey;
      }

      &:last-of-type {
        a {
          border-bottom: none;
        }
      }
    }
  }
}

@media screen and (min-width: 1024px) {
  .header {
    .container {
      flex-direction: row;
      justify-content: space-between;
    }
  }
}
